import {__}                                   from '@wordpress/i18n';
import {RichText, useBlockProps, InnerBlocks} from '@wordpress/block-editor';

export default function save(props) {
    const {attributes} = props;

    const {
        heading,
        headingHTMLTag,
        tocAdded,
        icon,
        customSVG,
        customSVGAlt,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        toggle,
        widthStyle,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        headingColor,
        headerBg,
        headingBorderColor,
        darkHeadingColor,
        darkHeaderBg,
        darkHeadingBorderColor,
        contentBg,
        darkContentBg,
        desktopHeaderPadding,
        tabletHeaderPadding,
        mobileHeaderPadding,
        desktopPadding,
        tabletPadding,
        mobilePadding
    } = attributes;

    let wrapperClassName = 'gb-wrap note-wrap none-padding';

    if (shadow) {
        wrapperClassName += ' yes-shadow';
    }
    if (toggle) {
        wrapperClassName += ' yes-toggle';

        if (widthStyle) {
            wrapperClassName += ` is-${widthStyle}`;
        }
    }
    if (!headingBorderColor && !headerBg) {
        wrapperClassName += ' no-h-spacing';
    }

    return (
        <div {...useBlockProps.save({
            className: wrapperClassName,
            style: {
                ...(headingColor && {'--heading-color': headingColor}),
                ...(headerBg && {'--header-bg': headerBg}),
                ...(headingBorderColor && {'--heading-border-color': headingBorderColor}),
                ...(darkHeadingColor && {'--dark-heading-color': darkHeadingColor}),
                ...(darkHeaderBg && {'--dark-header-bg': darkHeaderBg}),
                ...(darkHeadingBorderColor && {'--dark-heading-border-color': darkHeadingBorderColor}),
                ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
                ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
                ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
                ...(contentBg && {'--content-bg': contentBg}),
                ...(darkContentBg && {'--dark-content-bg': darkContentBg}),
                ...(borderStyle && {'--border-style': borderStyle}),
                ...(borderRadius && {'--border-radius': `${borderRadius}px`}),
                ...(borderWidth && {'--border-width': `${borderWidth.top || 0} ${borderWidth.right || 0} ${borderWidth.bottom || 0} ${borderWidth.left || 0}`}),
                ...(borderColor && {'--border-color': borderColor}),
                ...(darkBorderColor && {'--dark-border-color': darkBorderColor}),
                ...(desktopHeaderPadding && {'--desktop-header-padding': `${desktopHeaderPadding.top || 0} ${desktopHeaderPadding.right || 0} ${desktopHeaderPadding.bottom || 0} ${desktopHeaderPadding.left || 0}`}),
                ...(tabletHeaderPadding && {'--tablet-header-padding': `${tabletHeaderPadding.top || 0} ${tabletHeaderPadding.right || 0} ${tabletHeaderPadding.bottom || 0} ${tabletHeaderPadding.left || 0}`}),
                ...(mobileHeaderPadding && {'--mobile-header-padding': `${mobileHeaderPadding.top || 0} ${mobileHeaderPadding.right || 0} ${mobileHeaderPadding.bottom || 0} ${mobileHeaderPadding.left || 0}`}),
                ...(desktopPadding && {'--desktop-padding': `${desktopPadding.top || 0} ${desktopPadding.right || 0} ${desktopPadding.bottom || 0} ${desktopPadding.left || 0}`}),
                ...(tabletPadding && {'--tablet-padding': `${tabletPadding.top || 0} ${tabletPadding.right || 0} ${tabletPadding.bottom || 0} ${tabletPadding.left || 0}`}),
                ...(mobilePadding && {'--mobile-padding': `${mobilePadding.top || 0} ${mobilePadding.right || 0} ${mobilePadding.bottom || 0} ${mobilePadding.left || 0}`}),
            },
        })}>
            <div className="note-header gb-header">
                <span className="note-heading">
                    <span className="gb-heading heading-icon">
                        {customSVG ? (
                            <img src={customSVG} alt={customSVGAlt || __('note icon')}/>
                        ) : (
                            icon ? (
                                <i className={`rbi ${icon}`}/>
                            ) : null
                        )}
                    </span>
                    <RichText.Content
                        tagName={headingHTMLTag || 'h4'}
                        value={heading || ''}
                        className={`gb-heading${!tocAdded ? ' none-toc' : ''}`}
                    />
                </span>
                {toggle && (
                    <span className="gb-heading note-toggle"><i className="rbi-icon rbi-angle-down"/></span>
                )}
            </div>
            <div className="note-content gb-content">
                <InnerBlocks.Content/>
            </div>
        </div>
    );
}
