import {__} from '@wordpress/i18n';

import {
    PanelBody,
    PanelRow,
    BaseControl,
    SelectControl,
    RangeControl,
    ToggleControl,
    Button,
    TextControl,
    Dashicon,
    Tip,
    __experimentalBoxControl as BoxControl,
} from '@wordpress/components';

import {
    MediaUploadCheck,
    MediaUpload,
    InnerBlocks,
    RichText,
    PanelColorSettings,
    InspectorControls,
    useBlockProps
} from '@wordpress/block-editor';

export default function Edit(props) {

    const {attributes, setAttributes} = props;

    const {
        heading,
        headingHTMLTag,
        tocAdded,
        icon,
        customSVG,
        customSVGAlt,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        toggle,
        widthStyle,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        headingColor,
        headerBg,
        headingBorderColor,
        darkHeadingColor,
        darkHeaderBg,
        darkHeadingBorderColor,
        contentBg,
        darkContentBg,
        desktopHeaderPadding,
        tabletHeaderPadding,
        mobileHeaderPadding,
        desktopPadding,
        tabletPadding,
        mobilePadding
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    function setMediaAttribute(name, value) {
        setAttributes({[name]: value.url});
        setAttributes({[name + 'Alt']: value.alt});
    }

    function removeMediaAttribute(name) {
        setAttributes({[name]: ''});
        setAttributes({[name + 'Alt']: ''});
    }

    const iconOptions = [
        {label: __('Idea'), value: 'rbi-idea'},
        {label: __('Warning'), value: 'rbi-warning'},
        {label: __('Help'), value: 'rbi-help'},
        {label: __('Info'), value: 'rbi-information'},
        {label: __('Bell'), value: 'rbi-notification'},
        {label: __('Trend'), value: 'rbi-trending'},
        {label: __('Bookmark'), value: 'rbi-bookmark-fill'},
        {label: __('Secure'), value: 'rbi-secure'},
        {label: __('Star'), value: 'rbi-star'},
        {label: __('Heart'), value: 'rbi-heart'},
        {label: __('Chart'), value: 'rbi-chart'},
        {label: __('Pin'), value: 'rbi-pin'},
        {label: __('Not Allow'), value: 'rbi-not-allow'},
        {label: __('Flash'), value: 'rbi-flash'},
    ];

    const borderStyleOptions = [
        {label: __('None'), value: 'none'},
        {label: __('Solid'), value: 'solid'},
        {label: __('Dashed'), value: 'dashed'},
        {label: __('Dotted'), value: 'dotted'},
        {label: __('Double'), value: 'double'},
    ];

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('Div'), value: 'div'},
    ];

    const widthStyleOptions = [
        {label: __('Full Width'), value: 'fw'},
        {label: __('Inline'), value: 'inline'},
    ]

    let wrapperClassName = 'gb-wrap note-wrap none-padding';

    if (shadow) {
        wrapperClassName += ' yes-shadow';
    }
    if (toggle) {
        wrapperClassName += ' yes-toggle';

        if (widthStyle) {
            wrapperClassName += ` is-${widthStyle}`;
        }
    }
    if (!headingBorderColor && !headerBg) {
        wrapperClassName += ' no-h-spacing';
    }

    const blockProps = useBlockProps({
        className: wrapperClassName,
        style: {
            ...(headingColor && {'--heading-color': headingColor}),
            ...(headerBg && {'--header-bg': headerBg}),
            ...(headingBorderColor && {'--heading-border-color': headingBorderColor}),
            ...(darkHeadingColor && {'--dark-heading-color': darkHeadingColor}),
            ...(darkHeaderBg && {'--dark-header-bg': darkHeaderBg}),
            ...(darkHeadingBorderColor && {'--dark-heading-border-color': darkHeadingBorderColor}),
            ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
            ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
            ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
            ...(contentBg && {'--content-bg': contentBg}),
            ...(darkContentBg && {'--dark-content-bg': darkContentBg}),
            ...(borderStyle && {'--border-style': borderStyle}),
            ...(borderRadius && {'--border-radius': `${borderRadius}px`}),
            ...(borderWidth && {'--border-width': `${borderWidth.top || 0} ${borderWidth.right || 0} ${borderWidth.bottom || 0} ${borderWidth.left || 0}`}),
            ...(borderColor && {'--border-color': borderColor}),
            ...(darkBorderColor && {'--dark-border-color': darkBorderColor}),
            ...(desktopHeaderPadding && {'--desktop-header-padding': `${desktopHeaderPadding.top || 0} ${desktopHeaderPadding.right || 0} ${desktopHeaderPadding.bottom || 0} ${desktopHeaderPadding.left || 0}`}),
            ...(tabletHeaderPadding && {'--tablet-header-padding': `${tabletHeaderPadding.top || 0} ${tabletHeaderPadding.right || 0} ${tabletHeaderPadding.bottom || 0} ${tabletHeaderPadding.left || 0}`}),
            ...(mobileHeaderPadding && {'--mobile-header-padding': `${mobileHeaderPadding.top || 0} ${mobileHeaderPadding.right || 0} ${mobileHeaderPadding.bottom || 0} ${mobileHeaderPadding.left || 0}`}),
            ...(desktopPadding && {'--desktop-padding': `${desktopPadding.top || 0} ${desktopPadding.right || 0} ${desktopPadding.bottom || 0} ${desktopPadding.left || 0}`}),
            ...(tabletPadding && {'--tablet-padding': `${tabletPadding.top || 0} ${tabletPadding.right || 0} ${tabletPadding.bottom || 0} ${tabletPadding.left || 0}`}),
            ...(mobilePadding && {'--mobile-padding': `${mobilePadding.top || 0} ${mobilePadding.right || 0} ${mobilePadding.bottom || 0} ${mobilePadding.left || 0}`}),
        },
    });

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('General')} initialOpen={true}>
                    <SelectControl
                        label={__('Heading Tag')}
                        value={headingHTMLTag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('headingHTMLTag', value)}
                    />
                    <ToggleControl
                        label={__('Table of Content Included')}
                        checked={tocAdded}
                        onChange={(value) => setAttribute('tocAdded', value)}
                    />
                    <SelectControl
                        label={__('Select an Icon')}
                        value={icon}
                        options={iconOptions}
                        onChange={(value) => setAttribute('icon', value)}
                    />
                    <BaseControl label={__('or Upload SVG')}>
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={(value) => setMediaAttribute('customSVG', value)}
                                allowedTypes={['image', 'svg']}
                                value={customSVG}
                                render={({open}) => (
                                    <div>
                                        {!customSVG && (
                                            <Button className="button button-large" onClick={open}>
                                                {__('Upload Icon')}
                                            </Button>
                                        )}
                                        {customSVG && (
                                            <div>
                                                <img src={customSVG} alt="Icon"/>
                                                <Button className="button button-large" onClick={() => removeMediaAttribute('customSVG')}>
                                                    {__('Remove Icon')}
                                                </Button>
                                            </div>
                                        )}
                                    </div>
                                )}
                            />
                        </MediaUploadCheck>
                    </BaseControl>
                    <ToggleControl
                        label={__('Toggle Button')}
                        checked={toggle}
                        onChange={(value) => setAttribute('toggle', value)}
                        help={__('Collapse note content with show/hide toggle arrow button.')}
                    />
                    {toggle && (
                        <SelectControl
                            label={__('Width Layout')}
                            value={widthStyle}
                            options={widthStyleOptions}
                            onChange={(value) => setAttribute('widthStyle', value)}
                            help={__('Select a max width for the note block when enabled toggle button..')}
                        />
                    )}
                </PanelBody>
            </InspectorControls>
            <InspectorControls group="styles">
                <PanelBody title={__('Header')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="18"
                                value={desktopHeadingSize}
                                onChange={(value) => setAttribute('desktopHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="17"
                                value={tabletHeadingSize}
                                onChange={(value) => setAttribute('tabletHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="16"
                                value={mobileHeadingSize}
                                onChange={(value) => setAttribute('mobileHeadingSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: headingColor,
                                onChange: (value) => setAttribute('headingColor', value),
                                label: __('Text Color'),
                            },
                            {
                                value: headerBg,
                                onChange: (value) => setAttribute('headerBg', value),
                                label: __('Background'),
                            },
                            {
                                value: headingBorderColor,
                                onChange: (value) => setAttribute('headingBorderColor', value),
                                label: __('Bottom Border Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                    <PanelRow className="rb-tip-control"><Tip>{__('Click outside to see the update!')}</Tip></PanelRow>
                    <div className="res-padding-control">
                        <BoxControl
                            resetValues={{top: '15px', right: '30px', bottom: '15px', left: '30px'}}
                            label={<><Dashicon icon="desktop"/> {__('Desktop Padding')}</>}
                            values={desktopHeaderPadding}
                            onChange={(value) => setAttribute('desktopHeaderPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '15px', right: '25px', bottom: '15px', left: '25px'}}
                            label={<><Dashicon icon="tablet"/> {__('Tablet Padding')}</>}
                            values={tabletHeaderPadding}
                            onChange={(value) => setAttribute('tabletHeaderPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '15px', right: '20px', bottom: '15px', left: '20px'}}
                            label={<><Dashicon icon="smartphone"/> {__('Mobile Padding')}</>}
                            values={mobileHeaderPadding}
                            onChange={(value) => setAttribute('mobileHeaderPadding', value)}
                        />
                    </div>
                    <PanelColorSettings
                        title={__('Dark Mode - Color')}
                        colorSettings={[
                            {
                                value: darkHeadingColor,
                                onChange: (value) => setAttribute('darkHeadingColor', value),
                                label: __('Text Color'),
                            },
                            {
                                value: darkHeaderBg,
                                onChange: (value) => setAttribute('darkHeaderBg', value),
                                label: __('Background'),
                            },
                            {
                                value: darkHeadingBorderColor,
                                onChange: (value) => setAttribute('darkHeadingBorderColor', value),
                                label: __('Bottom Border Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Content')} initialOpen={false}>
                    <PanelRow className="rb-tip-control"><Tip>{__('Click outside to see the update!')}</Tip></PanelRow>
                    <div className="res-padding-control">
                        <BoxControl
                            resetValues={{top: '15px', right: '30px', bottom: '30px', left: '30px'}}
                            label={<><Dashicon icon="desktop"/> {__('Desktop Padding')}</>}
                            values={desktopPadding}
                            onChange={(value) => setAttribute('desktopPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '15px', right: '25px', bottom: '25px', left: '25px'}}
                            label={<><Dashicon icon="tablet"/> {__('Tablet Padding')}</>}
                            values={tabletPadding}
                            onChange={(value) => setAttribute('tabletPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '15px', right: '20px', bottom: '20px', left: '20px'}}
                            label={<><Dashicon icon="smartphone"/> {__('Mobile Padding')}</>}
                            values={mobilePadding}
                            onChange={(value) => setAttribute('mobilePadding', value)}
                        />
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: contentBg,
                                onChange: (value) => setAttribute('contentBg', value),
                                label: __('Background'),
                            },
                            {
                                value: darkContentBg,
                                onChange: (value) => setAttribute('darkContentBg', value),
                                label: __('Dark Mode - Background'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                    <PanelRow><Tip>{__('You can set content text color based on the block settings that you created.')}</Tip></PanelRow>
                </PanelBody>
                <PanelBody title={__('Border & Shadow')} initialOpen={false}>
                    <SelectControl
                        label={__('Border Style')}
                        value={borderStyle}
                        options={borderStyleOptions}
                        onChange={(value) => setAttribute('borderStyle', value)}
                        help={__('Please select the border color to see the border if you enable it.')}
                    />
                    {borderStyle && borderStyle !== 'none' && (
                        <>
                            <BoxControl
                                resetValues={{top: null, right: null, bottom: null, left: null}}
                                label={__('Border Width')}
                                values={borderWidth}
                                onChange={(value) => setAttribute('borderWidth', value)}
                            />
                            <PanelColorSettings
                                title={__('Border Color')}
                                colorSettings={[
                                    {
                                        value: borderColor,
                                        onChange: (value) => setAttribute('borderColor', value),
                                        label: __('Color'),
                                    },
                                    {
                                        value: darkBorderColor,
                                        onChange: (value) => setAttribute('darkBorderColor', value),
                                        label: __('Dark Mode - Color'),
                                    },
                                ]}
                            />
                        </>
                    )}
                    <RangeControl
                        label={__('Border Radius')}
                        value={borderRadius}
                        onChange={(value) => setAttribute('borderRadius', value)}
                        step={1}
                        max={100}
                        min={0}
                    />
                    <ToggleControl
                        label={__('Shadow')}
                        checked={shadow}
                        onChange={(value) => setAttribute('shadow', value)}
                    />
                </PanelBody>
            </InspectorControls>
            <div className="note-header gb-header">
                <span className="note-heading">
                    <span className="gb-heading heading-icon">
                        {
                            customSVG ? (
                                <img src={customSVG} alt={__('note icon')}/>
                            ) : (
                                icon ? (
                                    <i className={`rbi ${icon}`}/>
                                ) : null
                            )
                        }
                    </span>
                    <RichText
                        tagName={headingHTMLTag || 'h3'}
                        value={heading}
                        onChange={(value) => setAttribute('heading', value)}
                        className="gb-heading"
                        placeholder="Enter heading text..."
                    />
                </span>
                {toggle && (
                    <span className="gb-heading note-toggle"><i className="rbi-icon rbi-angle-down"/></span>
                )}
            </div>
            <div className="note-content gb-content">
                <InnerBlocks
                    templateLock={false}
                />
            </div>
        </div>
    )
}
